.card{
    width: 300px;
    height: 400px;
    border: 1px solid black;
    margin: 20px;
    position: relative;
}
.card-img{
    width: 100%;
    height: 85%;
}
.card-name{
   font-size: 30px;
   font-weight: 600;
   margin: 0;
}
.card-review{
    position: absolute;
    padding: 5px 20px;
    top: 0;
    right: 0;
    background: white;
    margin: 0;
}
.movies{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
}